.menu{
    width: auto;
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 2;
    padding: 80px 50px 10px 20px;
}
.nav > .nav_item{
    display: block;
    margin-top: 20px;
    margin-left: 20px;
}

.link:link {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    font-weight: 600;
}
.link:visited {
    color: #fff;
    text-decoration: none;
}
.link:hover{
    color: #ff4500;
    text-decoration: none;
}

@media only screen and (min-width: 992px) {
    .menu{
        width: auto;
        background-image: none;
        height: auto;
        position: static;
        z-index: 2;
        padding: 0px 0px ;
        float: right;
    }
    .nav > .nav_item{
        display: inline-block;
        margin-top: 20px;
        color: #fff
    }
    .link:link {
        color: #ff4500;
        text-decoration: none;
    }
    .link:visited {
        color: #ff4500;
        text-decoration: none;
    }
}