.navbar{
    z-index: 5;
    background-color: #fff;
    display:  flex;
    justify-content: space-between;
    align-items: center;
}
.logo{
    color: orangered;
    font-size: 20px;
    font-weight: 900;
}
.logo > span {
    color: #000080
}
.user{
    height: 30px;
    width: 30px;
    border-radius: 100px;
    margin-bottom: 10px;
}
.menu_btn{
    font-size: 30px;
    border: 0px;
    background-color: transparent;
    margin-left: 10px;
    padding: 0px;
    cursor: pointer;
}



@media only screen and (min-width: 992px) {
    .menu_btn{
        display: none;
    }
}